
'use client';

import { useState, useEffect } from 'react';
import { PageHeader } from "@/components/page-header";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
  CardFooter,
} from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Car, Truck, Save, Shield } from "lucide-react";
import type { VehicleType } from "@/lib/types";
import { useToast } from '@/hooks/use-toast';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';

// Rates are mirrored from the invoice page for consistency.
const initialPassengerRates: Partial<Record<VehicleType, number>> = {
    "SEDAN": 2.20,
    "PREMIUM_SEDAN": 2.80,
    "LUXURY_CAR": 10.00,
    "SUV": 3.00,
    "MPV": 3.50,
    "MINI_BUS": 3.50,
    "LIGHT_BUS": 4.50,
    "HEAVY_BUS": 4.50,
};

const initialShipperRates: Partial<Record<VehicleType, number>> = {
    "TRUCK_3T": 5.00,
    "TRUCK_5T": 5.50,
    "TRUCK_7T": 7.00,
    "TRUCK_10T": 7.50,
    "TRUCK_15T": 9.00,
    "TRAILER": 12.00,
    "FLATBED": 12.00,
    "CONTAINER_40FT": 15.00,
    "CONTAINER_60FT": 20.00,
    "CRANE_TRAILER": 18.00,
    "FORKLIFT": 30.00,
    "MOBILE_CRANE": 40.00,
    "BACKHOE_LOADER": 35.00,
    "BOBCAT": 25.00,
    "DUMP_TRUCK": 8.00,
    "WATER_TRUCK": 6.00,
};

export default function PricePage() {
  const { toast } = useToast();
  const [isAdminMode, setIsAdminMode] = useState(false);
  const [passengerRates, setPassengerRates] = useState(initialPassengerRates);
  const [shipperRates, setShipperRates] = useState(initialShipperRates);

  useEffect(() => {
    const checkAdminMode = () => {
      const adminStatus = localStorage.getItem('isAdminMode') === 'true';
      setIsAdminMode(adminStatus);
    };

    checkAdminMode();
    window.addEventListener('storage', checkAdminMode);
    return () => {
      window.removeEventListener('storage', checkAdminMode);
    };
  }, []);

  const handleRateChange = (
    segment: 'passenger' | 'shipper',
    type: VehicleType,
    value: string
  ) => {
    const numericValue = parseFloat(value) || 0;
    if (segment === 'passenger') {
      setPassengerRates(prev => ({ ...prev, [type]: numericValue }));
    } else {
      setShipperRates(prev => ({ ...prev, [type]: numericValue }));
    }
  };
  
  const handleSaveChanges = () => {
    toast({
        title: "Rates Saved",
        description: "Your new pricing has been updated across the system.",
    });
    // In a real app, you would make an API call here to persist the changes.
  }

  return (
    <>
      <PageHeader
        title="Vehicle Rate Card"
        description="Official per-kilometer pricing for all vehicle segments. Editable in Admin Mode."
        action={isAdminMode ? (
            <Button onClick={handleSaveChanges}>
                <Save className="mr-2 h-4 w-4" />
                Save Changes
            </Button>
        ) : null}
      />

       {!isAdminMode && (
         <Alert className="mb-6">
            <Shield className="h-4 w-4" />
            <AlertTitle>Read-Only Mode</AlertTitle>
            <AlertDescription>
                To edit pricing, please enable Admin Mode in the settings page.
            </AlertDescription>
         </Alert>
       )}

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        <Card>
          <CardHeader>
            <div className="flex items-center gap-3">
              <Car className="h-6 w-6 text-primary" />
              <div>
                <CardTitle>Passenger Vehicles</CardTitle>
                <CardDescription>Rates for all passenger transport types.</CardDescription>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Vehicle Type</TableHead>
                  <TableHead className="text-center">% Per</TableHead>
                  <TableHead className="text-right">Rate (SAR/KM)</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {Object.entries(passengerRates).map(([type, rate]) => (
                  <TableRow key={type}>
                    <TableCell className="font-medium">
                      {type.replace(/_/g, " ")}
                    </TableCell>
                    <TableCell className="text-center">
                       <Input 
                         type="text" 
                         defaultValue="-" 
                         className="min-w-[60px] text-center" 
                         readOnly={!isAdminMode}
                       />
                    </TableCell>
                    <TableCell className="text-right">
                       <Input 
                         type="number" 
                         value={rate?.toFixed(2) || '0.00'}
                         onChange={(e) => handleRateChange('passenger', type as VehicleType, e.target.value)}
                         className="min-w-[100px] text-right" 
                         readOnly={!isAdminMode}
                       />
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
             <div className="flex items-center gap-3">
                <Truck className="h-6 w-6 text-primary" />
                <div>
                    <CardTitle>Shipper</CardTitle>
                    <CardDescription>Rates for all shipper transport.</CardDescription>
                </div>
            </div>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Vehicle Type</TableHead>
                  <TableHead className="text-center">% Per</TableHead>
                  <TableHead className="text-right">Rate (SAR/KM)</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {Object.entries(shipperRates).map(([type, rate]) => (
                  <TableRow key={type}>
                    <TableCell className="font-medium">
                      {type.replace(/_/g, " ")}
                    </TableCell>
                     <TableCell className="text-center">
                       <Input 
                         type="text" 
                         defaultValue="-" 
                         className="min-w-[60px] text-center" 
                         readOnly={!isAdminMode}
                       />
                    </TableCell>
                    <TableCell className="text-right">
                       <Input 
                         type="number" 
                         value={rate?.toFixed(2) || '0.00'}
                         onChange={(e) => handleRateChange('shipper', type as VehicleType, e.target.value)}
                         className="min-w-[100px] text-right" 
                         readOnly={!isAdminMode}
                       />
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </div>
    </>
  );
}
